.container {
    width: 100%;
    height: 100%;
    background-image: url("../components/Avengers.jpg");
    display: flex;
    justify-content: center;
    align-items: center;
}


.loading {
    color: green;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 45px;
    color: rgba(104, 228, 0, 1);
    font-family: "Press Start 2P", cursive;
    line-height: 2.5em;
}

.menu {
    display: grid;
    grid-template-columns: repeat(3, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 70px;
}

@media screen and (max-width: 1540px) {
    .menu {
        display: flex;
        flex-direction: column;
    }
}