.movie_title {
    text-align: center;
}

.butDiv {
    text-align: right;
}

.button {
    font-size: 18px;
    border-radius: 5px;
}

.movie_img {
    width: 85%;
    height: 600px;
    max-height: 600px;
    min-height: 600px;
    box-shadow: 0px 20px 40px -12px;
}


.movie_title a {
    color: whitesmoke;
    text-decoration: none;
}

.genre {
    width: 100%; /*크기는 400일거임*/
}

@media screen and (max-width: 1734px) {
    .genre {
        display: none;
    }
}

.summary {
    color: #3565c9;
    font-size: 25px;
    line-height: 1.5em;
    font-weight: 800;
    padding: 0 20px;
}

.ul {
    list-style: none;
    display: flex;
    flex-wrap: nowrap;/* 가로로 한줄에 배치 */
    justify-content: space-evenly;
    padding-left: 0px;
    font-size: 25px;
    line-height: 30px;
    font-weight: 700;
}

.li {
    margin-right: 30px;
    flex-wrap: nowrap;
}


