body {
    background-color: beige;
}

.loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 35px;
    color: rgba(104, 228, 0, 1);
    font-family: "Press Start 2P", cursive;
    line-height: 2.5em;
}

.header {
    text-align: center;
    height: 80px;
    font-size: 45px;
    font-weight: bold;
    padding: 20px 0px;
}

.header2 {
    padding: 20px;
}

.detail_img {
    margin: 0 auto;
    border-radius: 10px;
    padding: 0px 30px
}

.title {
    text-align: center;
    color: black
}

.genres {
    display: flex;
}

.ul {
    list-style: none;
    display: flex;
    justify-content: center;
    padding-left: 0px;
    font-size: 35px;
    line-height: 30px;
    font-weight: 700;
}

.li {
    margin-right: 30px;
}

.rating {
    color: blue;
    font-weight: bold;
    font-size: 40px;
}

.movie_detail {
    position: relative;
    top: 50%;
    bottom: 50%;
    display: flex;
    height: 100vh;
    width: 100%;
}

.detail_left {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0px auto;
}

.detail_right {
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center; /* 수직방향의 주축이므로 세로의 정가운데 의미*/
}

.detail_des {
    color: #3565c9;
    width: 70%;
    font-size: 25px;
    line-height: 1.5em;
    font-weight: 800;
}